import { Component, OnInit } from '@angular/core';

declare const cordova: any;
declare const navigator: any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  ref: any;
  browser: any;
  dialog: any;

  constructor() {
    this.browser = cordova.InAppBrowser;
    this.dialog = navigator.notification;
  }

  ngOnInit() {
  }

  getRedirect(target) {
    const options = {
      location: 'yes',
      hidden: 'no',
      clearcache: 'yes',
      clearsessioncache: 'yes',
      zoom: 'no',
      hardwareback: 'no',
      mediaPlaybackRequiresUserAction: 'yes',
      closebuttoncaption: 'close',
      disallowoverscroll: 'yes',
      enableViewportScale: 'yes',
      allowInlineMediaPlayback: 'yes',
      keyboardDisplayRequiresUserInteraction: 'no',
      suppressIncrementalRendering: 'yes',
      presentationstyle: 'fullscreen',
      transitionstyle: 'coververtical',
      toolbarposition: 'bottom',
      fullscreen: 'yes'
    };
    let optStr = this.getOptions(options);
    let url = '';
    switch (target) {
      case 'tutorial':
        url = 'https://angular.io/tutorial';
        this.dialog.confirm(
            'Are you sure?',         // message
            this.redirectToURL(url, optStr),          // callback
            'Open Browser',            // title
            ['OK', 'Cancel']        // buttons
        );
        break;
      case 'cli' :
        url = 'https://angular.io/cli';
        this.redirectToURL(url, optStr);
        break;
      case 'blog' :
        url = 'https://blog.angular.io/';
        this.redirectToURL(url, optStr);
        break;
    }
  }

  callBack(e) {
    console.log(e);
  }

  redirectToURL(url, optStr) {
    this.ref = this.browser.open(url, '_blank', optStr);
    this.ref.addEventListener('loadstart', this.callBack.bind(this));
    this.ref.removeEventListener('loadstart', this.callBack.bind(this));
  }

  getOptions(obj): string {
    // tslint:disable-next-line:forin
    let str = '';
    for (let key in obj) {
      str += key + '=' + obj[key] + ',';
    }
    return str;
  }

}
