import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

declare const window: any;
declare const navigator: any;
declare const cordova: any;
declare const device: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  google: any;
  dialog: any;
  notify: any;

  constructor(private router: Router) {
    this.google = window.plugins.googleplus;
    this.dialog = navigator.notification;
    this.notify = cordova.plugins;
  }

  ngOnInit() {
    console.log(device.model);
  }

  loginWithGoogle() {
    const el = this;
    this.google.isAvailable(
      (available) => {
        if (available) {
          // show the Google+ sign-in button
          el.getAuthenticate('google');
        } else {
          el.dialog.alert(
            'You can not sign in with Google+',   // message
            el.alertDismissed,                   // callback
            'Alert',                            // title
            'Ok'                               // buttonName
        );
        }
      }
    );
  }

  getAuthenticate(type) {
    switch (type) {
      case 'google' :
        this.google.login({
          scopes: '',
          webClientId: '280718459940-hvkjenioqrb3c3pvjmnja78ef7nvpjln.apps.googleusercontent.com',
          offline: true
        }, this.loginSuccess(event, this), this.loginError);
        break;
      case 'facebook' :
        break;
    }
  }

  loginSuccess(event, el) {
    el.notify.notification.local.setDefaults({
      led: {color: '#FF00FF', on: 500, off: 500},
      vibrate: true
    });
    el.notify.notification.local.schedule({
      title: 'Google Login Notification',
      text: 'You have logged in successful!!',
      foreground: true,
      icon: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTzfXKe6Yfjr6rCtR6cMPJB8CqMAYWECDtDqH-eMnerHHuXv9egrw'
    });
    // alert(JSON.stringify(event));
    console.log(event);
    el.router.navigate(['home']);
  }

  loginError(error) {
    alert('Error: ' + error);
  }

  alertDismissed(ev) {
    console.log(ev);
  }
}
